body {
  margin: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background: #405a55;
}

.App {
  display: grid;
  grid-template-columns: 800px;
  justify-content: center;
  gap: 10px;
}

.app-card {
  padding: 10px;
  background: #f5b797;
  border-radius: 20px;
  box-shadow: 8px 10px 51px -10px rgba(32,46,44,1);
}

.app-card * {
  margin: 0;
}

.user-info__name {
  text-align: center;
  font-size: 22px;
  margin-bottom: 10px;
  margin-top: 15px;
}

.user-info__contact-info {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  margin-bottom: 15px;
  color: #352925;
}

.contact-info__address {
  text-align: right;
}

.user-post-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.user-post {
  text-align: left;
  background: #352925;
  color: white;
  padding: 10px;
}

.user-post__body-title {
  font-size: 13px;
  margin-bottom: 5px;
}

.user-post__body-text {
  font-size: 12px;
  margin-bottom: 10px;
}

.user-post__body-comment-label {
  font-size: 12px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.user-post__comment {
  font-size: 12px;
  margin-bottom: 7px;
  border: 1px dotted;
  padding: 5px;
}

.comment-name {
  font-weight: 500;
}

.comment-email {
  font-style: italic;
}
